import * as tf from '@tensorflow/tfjs';
import { AppTF } from '..';
export class Implementation {
    app: AppTF;
    constructor(app: AppTF) {
        this.app = app;
        this.Init();
    }
    Init() {
        this.app.divText.style.margin = '10px';
        this.app.divText.innerHTML = '<p>tfjs: ' + tf.version['tfjs'] + '</p>' +
            '<p>tfjs-converter: ' + tf.version['tfjs-converter'] + '</p>' +
            '<p>tfjs-core: ' + tf.version['tfjs-core'] + '</p>' +
            '<p>tfjs-data: ' + tf.version['tfjs-data'] + '</p>' +
            '<p>tfjs-layers: ' + tf.version['tfjs-layers'] + '</p>';
        console.log(tf.version);
    }
}